var dissolve = require('yap-require-hook').addReference(require('dissolve'), 'dissolve');
var utf8 = require('yap-require-hook').addReference(require('utf8'), 'utf8');
var base64 = require('yap-require-hook').addReference(require('base64-js'), 'base64-js');    // which is also used in `buffer` module, that is added to bundle-js by Browserify
var wpa = require('yap-require-hook').addReference(require('./livescript/wpa'), './livescript/wpa');
var utils = require('yap-require-hook').addReference(require('./utils'), './utils');
var ASCII = utils.ASCII;
var APPLY = utils.APPLY;
var COPY2ARRAY = utils.COPY2ARRAY;

//
// FOOP-specific helpers
//

function PARSE_SSID_LIST(data, done) {
  var parser;
  var records = [];
  data = new Buffer(data);
  parser = dissolve();
  parser.loop(function(end) {
    this.buffer('mac', 6);
    this.uint8('channel');
    this.uint8('quality');
    this.uint8('signal');
    this.uint8('type');
    this.uint8('len')
      .tap(function() { this.buffer('ssid', this.vars.len); })
      .tap(function() {
        this.push(this.vars);
        this.vars = {};
      });
  });
  parser.on('readable', function() {
    while (true) {
      evt = parser.read();
      if (typeof evt != 'undefined' && evt !== null) {
        // console.log("evt => " + JSON.stringify(evt));
        evt.mac = evt.mac.toString('hex').toUpperCase();
        evt.signal = evt.signal - 256;
        evt.ssid_hex = evt.ssid.toString('hex').toUpperCase();
        evt.ssid = COPY2ARRAY(evt.ssid, [], String.fromCharCode).join("");
        evt.ssid = utf8.decode(evt.ssid);
        records.push(evt);
      }
      else {
        break;
      }
    }
  });
  parser.write(data, function() {
    console.log("written");
    return done(null, records);
  });
}



module.exports = {

  //
  // Modules
  //
  utf8: utf8,
  dissolve: dissolve,
  base64: base64,

  ASCII: ASCII,
  APPLY: APPLY,
  COPY2ARRAY: COPY2ARRAY,

  //
  // WPA related helper functions (browser/app)
  //
  GEN_WPA2_CONFIG_WITH_SSID: wpa.GEN_WPA2_CONFIG_WITH_SSID,
  GEN_DEFLATED_WITH_SSID_PWD_PSK: wpa.GEN_DEFLATED_WITH_SSID_PWD_PSK,
  //
  // FOOP-specific helpers (browser/app)
  //
  PARSE_SSID_LIST: PARSE_SSID_LIST
}
