function ASCII(x) {
  return x.charCodeAt(0);
}

function CHECK_EXIST(x) {
  return (typeof x != 'undefined') && (x !== null);
}

function EXIST_OR_DEFAULT(x, defaultValue) {
  return CHECK_EXIST(x) ? x : defaultValue;
}

function APPLY(x, func) {
  return CHECK_EXIST(func) ? func(x) : x;
}

function APPLY_ARGS(func, ctx, args) {
  return CHECK_EXIST(func) ? func.apply(ctx, args) : null
}

function COPY2ARRAY(buffer, initial, transform) {
  var xs, i, len, x;
  xs = [];
  if (typeof initial != 'undefined' && initial !== null) {
    for (i = 0, len = initial.length; i < len; ++i) {
      x = initial[i];
      x = APPLY(x, transform);
      xs.push(x);
    }
  }
  for (i = 0, len = buffer.length; i < len; ++i) {
    x = buffer[i];
    x = APPLY(x, transform);
    xs.push(x);
  }
  return xs;
}


module.exports = {
  ASCII: ASCII,
  APPLY: APPLY,
  APPLY_ARGS: APPLY_ARGS,
  CHECK_EXIST: CHECK_EXIST,
  EXIST_OR_DEFAULT: EXIST_OR_DEFAULT,
  COPY2ARRAY: COPY2ARRAY
}

