var base64 = require('yap-require-hook').addReference(require('base64-js'), 'base64-js');    // which is also used in `buffer` module, that is added to bundle-js by Browserify
var formatter = require('yap-require-hook').addReference(require('format-util'), 'format-util');
var BleModem = require('yap-require-hook').addReference(require('./BleModem'), './BleModem');
var utils = require('yap-require-hook').addReference(require('./utils'), './utils');
var ASCII = utils.ASCII;
var APPLY_ARGS = utils.APPLY_ARGS;
var COPY2ARRAY = utils.COPY2ARRAY;
var CHECK_EXIST = utils.CHECK_EXIST;
var EXIST_OR_DEFAULT = utils.EXIST_OR_DEFAULT;
var DUMMY_HEAD = 0x00;


function COMPOSE_PACKET(packet, b64) {
  // Remove the DUMMY head byte
  //
  var xs, ys;
  xs = COPY2ARRAY(packet);
  xs.shift();
  if (b64) {
    ys = COPY2ARRAY(base64.fromByteArray(COPY2ARRAY(xs)), [], ASCII);
  }
  else {
    ys = xs;
  }
  return ys;
}


function DECOMPOSE_PACKET(packet, b64) {
  // Add the DUMMY head byte
  //
  var xs, ys;
  if (b64) {
    xs = (typeof(packet) == 'string') ? packet : (new Buffer(COPY2ARRAY(packet))).toString();
    xs = COPY2ARRAY(base64.toByteArray(xs));
  }
  else {
    xs = packet;
  }
  ys = COPY2ARRAY(xs, [DUMMY_HEAD]);
  ys = new Buffer(ys);
  return ys;
}

function LOGGER(context, args, inner) {
  if (context.disabled) { return; }
  if (!(context.verbose)) { return; }
  var logger = context.logger;
  var prefix = EXIST_OR_DEFAULT(inner, false);
  var message = formatter.apply(null, args);
  prefix = prefix ? "modem  " : "wrapper";
  return logger("[" + context.index + "]." + prefix + ": " + message);
}

function NOTIFY_ERROR(context, message) {
  if (context.disabled) { return; }
  LOGGER(context, [message], false);
  return APPLY_ARGS(context.handlers.on_error, context, [message]);
}

function WRITE_PACKET(context, packet) {
  if (context.disabled) { return; }
  var writer = context.writer;
  var b64 = context.b64;
  return writer(COMPOSE_PACKET(packet, b64));
}

function START_TIMER(context, func, milliseconds) {
  if (context.disabled) { return; }
  var on_error = context.handlers.on_error;
  var timer = context.timer;

  /* 2nd timer is created, that is unexpected behavior */
  if (CHECK_EXIST(timer)) {
    return NOTIFY_ERROR(context, "WARNING!! unexpected 2nd timer to start");
  }

  var callback = function() {
    var error = null;
    try { return func(); }
    catch (err) { error = err; }
    CLEAR_TIMER(context, context.timer);
    APPLY_ARGS(on_error, context, [error]);
  };

  context.timer = setInterval(callback, milliseconds);
  // LOGGER(context, "timer => " + context.timer, true);
  return context.timer;
}

function CLEAR_TIMER(context, timer) {
  if (context.disabled) { return; }
  var t = context.timer;
  if (!CHECK_EXIST(t)) {
    return NOTIFY_ERROR(context, "WARNING!! clear timer but never created");
  }
  if (t !== timer) {
    return NOTIFY_ERROR(context, "WARNING!! clear timer that is not created by context: " + t + " v.s. " + timer);
  }
  else {
    clearInterval(t);
    context.timer = null;
  }
}

// The currying helper function generated by Livescript
//
function CURRY$(f, bound){
  var ctx,
  _curry = function(args) {
    return f.length > 1 ? function(){
      var params = args ? args.concat() : [];
      ctx = bound ? ctx || this : this;
      return params.push.apply(params, arguments) <
          f.length && arguments.length ?
        _curry.call(ctx, params) : f.apply(ctx, params);
    } : f;
  };
  return _curry();
}

function DELIVERY_CALLBACK(context, results) {
  if (context.disabled) { return; }
  return context.cb(results);
}

function GENERATE_PLATFORM_IF(x) {
  var logger_currying = CURRY$(LOGGER);
  var start_timer_currying = CURRY$(START_TIMER);
  var clear_timer_currying = CURRY$(CLEAR_TIMER);
  var write_packet_currying = CURRY$(WRITE_PACKET);
  var platform = {
    logger    : logger_currying(x),
    writer    : write_packet_currying(x),
    startTimer: start_timer_currying(x),
    clearTimer: clear_timer_currying(x),
    dummy     : false
  };
  return platform;
}




var Wrapper = module.exports = function Wrapper(opts, cb) {
  var self = this;
  self.id = (new Date()).getTime();
  self.context_index = 0;
  self.opts = opts;
  self.cb = cb;

  self.reset = function() {
    var opts = self.opts;
    if (CHECK_EXIST(self.context)) {
      // Clean-up previous instance
      CLEAR_TIMER(self.context, self.context.timer);
      self.context.disabled = true;
    }
    self.context_index = self.context_index + 1;

    var context = {
      disabled: false,
      index: "" + self.id + "." + self.context_index,
      b64: EXIST_OR_DEFAULT(opts['base64'], false),
      verbose: EXIST_OR_DEFAULT(opts['verbose'], true),
      logger: opts['logger'],
      writer: opts['writer'],
      timer: null,
      cb: self.cb,
      handlers: {
        on_error: EXIST_OR_DEFAULT(opts['on_error'], null),
        dummy   : false
      }
    }

    var platform = GENERATE_PLATFORM_IF(context);

    var configs = {
      payloadSize: EXIST_OR_DEFAULT(opts['payloadSize'], 10),
      packetHead: DUMMY_HEAD,
      platform: platform
    };

    var delivery_cb_currying = CURRY$(DELIVERY_CALLBACK);
    var delivery_cb = delivery_cb_currying(context);

    self.context = context;
    self.inner = new BleModem(configs, delivery_cb);
  }

  self.reset();

  self.transfer = function(name, data, done) {
    try {
      // console.error("typeof(data) => " + typeof(data));
      // console.error("data => " + JSON.stringify(data));
      self.inner.transfer(name, new Buffer(data), done);
    }
    catch (error) {
      var on_error = self.context.handlers.on_error;
      CLEAR_TIMER(self.context, self.context.timer);
      APPLY_ARGS(on_error, self, [error]);
    }
  }

  self.abort = function() { self.inner.abort(); }

  self.stop = function() {
    if (CHECK_EXIST(self.timer)) {
      CLEAR_TIMER(self.context, self.context.timer);
    }
  }

  self.handlePacket = function(p) {
    var ctx = self.context;
    try {
      return self.inner.handlePacket(DECOMPOSE_PACKET(p, ctx.b64));
    }
    catch (error) {
      // console.log("handlePacket: error => " + error);
      // console.log("handlePacket: " + error.stack);
      var on_error = ctx.handlers.on_error;
      CLEAR_TIMER(ctx, ctx.timer);
      APPLY_ARGS(on_error, self, [error]);
    }
  }
};
