function atoi(char){
  return char.charCodeAt(0)
}

function itoa(n){
  return String.fromCharCode(n)
}

function chksum(buf) {
    var sum = 0;
    for (var i = 0; i < buf.length; i++) {
        sum += buf[i];
    }
    sum = sum & 0xff;
    return sum;
}

var BleModem = module.exports = function BleModem(opts, appRecvCallback) {
  var self = this;

  /** Default settings */
  self.packetHead = 0x26; // '&'
  self.payloadSize = 17;
  self.writer = null;
  self.logger = null;

  if (typeof opts != 'undefined' && opts !== null) {
    var platform = opts['platform'];
    if (opts['packetHead'] != null)   { self.packetHead = opts['packetHead'];   }
    if (opts['payloadSize'] != null)  { self.payloadSize = opts['payloadSize']; }
    if (platform['writer'] != null)   { self.writer = platform['writer'];       }
    if (platform['logger'] != null)   { self.logger = platform['logger'];       }
    self.startTimer = platform['startTimer'];
    self.clearTimer = platform['clearTimer'];
  }
  self.packetLength = self.payloadSize + 3; //head + cmd/seq + payload + chksum
  self.dataNameLength = self.payloadSize - 3; //2 bytes for length, 1 byte for extra command byte (0x00)

  self.maxSize = 0xfe * self.payloadSize;
  self.stateTimeout = 1000; //timeout to retry
  self.retryCount = 4; //fixed retry count for Abort and SendConformation, where we don't wait for ack from peer
                       //4 times shuold be enough to deal with 75% packet loss..
  self.appRecvCallback = appRecvCallback
  var FSM = require('yap-require-hook').addReference(require('./state-machine.js'), './state-machine.js')
  self.fsm = FSM.create({
    initial: 'IDLE',
    // error: function(eventName, from, to, args, errorCode, errorMessage) {
    //   self.log('Error: event ' + eventName + ':- ' + errorMessage)
    //   return 'Error: event ' + eventName + ':- ' + errorMessage;
    // },
    events: [
      { name: 'AppTransferReq',    from: 'IDLE',  to: 'INIT_TO_SEND' },     //retry if stuck
      { name: 'GotPeerCts',        from: 'INIT_TO_SEND',  to: 'SENDING' },
      { name: 'FinishSending',      from: 'SENDING',  to: 'ALL_SENT' },     //retry if stuck
      { name: 'GotResendReq',      from: 'ALL_SENT',  to: 'RESEND_PKT' },
      { name: 'GotResendReq',      from: 'RESEND_PKT',  to: 'RESEND_PKT' },
      { name: 'GotPeerConfirmation',    from: 'RESEND_PKT',  to: 'IDLE' },
      { name: 'GotPeerConfirmation',    from: 'ALL_SENT',  to: 'IDLE' },
      { name: 'Abort', from: ['INIT_TO_SEND', 'SENDING', 'ALL_SENT', 'RESEND_PKT'], to:'IDLE'},

      { name: 'GotPeerSendReq',    from: 'IDLE',  to: 'INIT_TO_RECV' },      //retry if stuck
      { name: 'GotPeerDataPkt',    from: 'INIT_TO_RECV',  to: 'RECV_BULK' },
      { name: 'GotPeerDataPkt',    from: 'RECV_BULK',  to: 'RECV_BULK' },
      { name: 'GotPeerEndPkt',     from: 'RECV_BULK',  to: 'CHECK' },
      { name: 'ReqResendPkt',      from: 'CHECK',  to: 'WAIT_REGET' },     //retry if stuck
      { name: 'GotPeerDataPkt',    from: 'WAIT_REGET',  to: 'CHECK' },
      { name: 'SendConfirmation',  from: 'CHECK',  to: 'IDLE' },          //retry if stuck
      { name: 'Abort', from: ['RECV_BUIL', 'CHECK', 'REGET_PKT'], to:'IDLE'},
    ]
  })

  //external api BleModem.abort()
  self.abort = function(){
    var pkt = Buffer(self.packetLength).fill(0)
    pkt[0] = self.packetHead
    pkt[1] = 0xff
    pkt[2] = 0x13 //abort request
    pkt[self.packetLength-1] = chksum(pkt)
    self.writePacket(pkt)
    for(var i=0;i<self.retryCount;i++){
      self.fsm.Abort()
    }
  }
  self.fsm.onAbort = function(event, from, to){
    self.log('got abort request from state: %s', from)
    self.packetsToSend = undefined
    self.packetsToRecv = undefined
    self.packetsReceived = undefined
  }
  self.segmentData = function(data){
    var psize = self.payloadSize
    nRoundedPackets = Math.floor(data.length/psize)
    nRes = data.length%psize
    self.packetsToSend = []
    for(var i=0;i<nRoundedPackets;i++){
      self.packetsToSend.push(data.slice(i*psize, i*psize+psize))
    }
    if(nRes>0){self.packetsToSend.push(data.slice(nRoundedPackets*psize, nRoundedPackets*psize+ nRes))}
    self.log('packets to send: %s', JSON.stringify(self.packetsToSend))
  }

  self.prepareRecvPackets = function(sName, nLength){
    var psize = self.payloadSize
    nRoundedPackets = Math.floor(nLength/psize)
    nRes = nLength%psize
    self.packetsToRecv = []
    self.packetReceived = []
    for(var i=0;i<nRoundedPackets;i++){
      self.packetsToRecv.push(Buffer(psize).fill(0))
      self.packetReceived.push(false)
    }
    if(nRes>0){
      self.packetsToRecv.push(Buffer(nRes).fill(0))
      self.packetReceived.push(false)
    }
    self.log('packets to recv: %s', JSON.stringify(self.packetsToRecv))
    self.receiveDataName = sName
  }

  // self.fsm.onbeforeAppTransferReq = function(event, from, to, name, data){
  self.fsm.onenterINIT_TO_SEND = function(event, from, to, name, data){
    // self.log('onAppTransferReq', name, data)
    if(data.length > self.maxSize) throw "Error: data length " + data.length + "exceeds protocol limit of " + self.maxSize
    //prepare packet segmentation
    var pHead = Buffer(3)
    pHead[0] = self.packetHead //'&'
    pHead[1] = 0xff                // command packet
    pHead[2] = 0x00                // request to start

    var pName = Buffer(self.dataNameLength).fill(0)
    pName.write(name.slice(0,self.dataNameLength))

    var pLen = Buffer(2)
    pLen.writeUInt16BE(data.length, 0)

    packet = new Buffer(self.packetLength).fill(0)
    tmp = new Buffer.concat([pHead, pName, pLen])
    tmp.copy(packet)
    packet[self.packetLength-1] = chksum(tmp)
    //segmentation of data
    self.segmentData(data)
    self.writePacket(packet)
    self.lastPacket = packet  //whenever possible to retry, set this
  }

  self.fsm.onafterevent = function(event,from,to){
    if( self.lastPacket != undefined){
      self.log('startTimer - event:%s, from:%s, to:%s, pkt:%s', event, from, to, JSON.stringify(self.lastPacket))
      var packet
      self.intervalHandle = self.startTimer(function(){
        packet = self.lastPacket;
        self.log('retry packet: %s', JSON.stringify(packet))
        self.writePacket(packet)
      }, self.stateTimeout)
    }
  }

  self.fsm.onleavestate = function(event,from,to){
    self.log('onleavestate - event:%s, from:%s, to:%s, pkt:%s', event, from, to, JSON.stringify(self.lastPacket))
    self.lastPacket = undefined
    self.clearTimer(self.intervalHandle)
  }



  // self.fsm.onbeforeGotPeerSendReq = function(event, from, to, packet){
  self.fsm.onenterINIT_TO_RECV = function(event, from, to, packet){
    // decode the packet
    var pName = packet.slice(3,3+self.dataNameLength)
    var strName =""
    for(var i =0;i<self.dataNameLength;i++){
      if (pName[i] != 0x0){ strName += itoa(pName[i]) }else{ break}
    }
    pLen = packet.slice(self.dataNameLength+3,self.dataNameLength+5)
    nLen = pLen.readUInt16BE(0)
    self.log('got send request, name: %s, size: %d', strName, nLen)
    self.prepareRecvPackets(strName,nLen)

    var ctsPacket = Buffer(self.packetLength).fill(0)
    ctsPacket[0] = self.packetHead
    ctsPacket[1] = 0xff
    ctsPacket[2] = 0x10  //ctsPacket
    ctsPacket[self.packetLength-1] = chksum(ctsPacket)

    self.writePacket(ctsPacket)
    self.lastPacket = ctsPacket  //whenever possible to retry, set this
  }

  self.sendPktSeq = function(i){
    var pHead = Buffer(2)
    pHead[0] = self.packetHead
    pHead[1] = i
    var pktlen = self.packetsToSend[i].length
    var packet = Buffer.concat([pHead, self.packetsToSend[i], Buffer(self.packetLength-2-pktlen).fill(0)])
    packet[self.packetLength-1] = chksum(packet)
    self.writePacket(packet)
  }

  self.fsm.onGotPeerCts = function(event, from, to){
    // go on sending all
    for(var i=0;i<self.packetsToSend.length;i++){
      self.sendPktSeq(i)
    }

    setTimeout(function(){self.fsm.FinishSending()}, 50)
  }

  // self.fsm.onbeforeFinishSending = function(event, from, to){
  self.fsm.onenterALL_SENT = function(event, from, to){
    var endpkt = Buffer(self.packetLength).fill(0)
    endpkt[0] = self.packetHead
    endpkt[1] = 0xff
    endpkt[2] = 0x01
    endpkt[self.packetLength-1] = chksum(endpkt)

    self.writePacket(endpkt)
    // self.lastPacket = endpkt  //whenever possible to retry, set this
  }

  self.fsm.onGotPeerDataPkt = function(event, from, to, packet){
    var seq = packet[1]
    var content = packet.slice(2,2+self.payloadSize)
    content.copy(self.packetsToRecv[seq])
    self.packetReceived[seq] = true
  }

  self.fsm.onCHECK = function(event, from, to){
    //check the received packet and ask for resend if needed
    resendPktQue = []
    for(var i =0; i<self.packetsToRecv.length; i++){
      if(self.packetReceived[i] === false ){resendPktQue.push(i)}
    }
    self.log('missing packet count=%d, queue: ', resendPktQue.length, resendPktQue)
    if(resendPktQue.length === 0){
      setTimeout(function(){self.fsm.SendConfirmation()},50)
    }else{
      setTimeout(function(){self.fsm.ReqResendPkt(resendPktQue)},50)
    }
  }
  // self.fsm.onbeforeReqResendPkt = function(event, from, to, pktQue){
  self.fsm.onenterWAIT_REGET = function(event, from, to, pktQue){
    var pHead = Buffer(3)
    pHead[0] = self.packetHead
    pHead[1] = 0xff
    pHead[2] = 0x11 //resend request
    var resendPkts = Buffer(self.packetLength-4).fill(0xff)  //NOTE, can't use 0 here since it's a valid packet ID
    pktQueBuf = Buffer(pktQue.slice(0,self.packetLength-4))
    pktQueBuf.copy(resendPkts)  //0xff padded packet
    var pkt = Buffer.concat([pHead, resendPkts, Buffer(1).fill(0)])
    pkt[self.packetLength-1] = chksum(pkt)

    self.writePacket(pkt)
    self.lastPacket = pkt  //whenever possible to retry, set this
  }

  self.fsm.onbeforeSendConfirmation = function(event, from, to){
    var pkt = Buffer(self.packetLength).fill(0)
    pkt[0] = self.packetHead
    pkt[1] = 0xff
    pkt[2] = 0x12
    pkt[self.packetLength-1] = chksum(pkt)

    for(var i=0;i<self.retryCount;i++){self.writePacket(pkt)}  // just retry n times
    // self.lastPacket = pkt  //whenever possible to retry, set this
    self.appRecvCallback({'name': self.receiveDataName, 'data': Buffer.concat(self.packetsToRecv)})
  }

  self.fsm.onGotResendReq = function(event,from,to,packet){
    var pktSeq = packet[3]
    //resend only the first valid (none 0xff) packet ID instead
    // wait for next resend request
    self.sendPktSeq(pktSeq)
  }

  self.fsm.onGotPeerConfirmation = function(event,from,to){
    if(typeof self.sendFinishCb === 'function'){
      self.sendFinishCb(null);
    }else{
      self.log('send complete; no callback registered');
    }
  }
  // low level packet handler for all incoming '&' packets
  // packet template:  zero padding before 'chk'
  //  0   1       2                  17  18   19
  // '&' [00~FE] data[17]                     chk
  //             msg                          chk
  //  '&' FF     0x00: s -> r, request to start
  //                   3
  //                   name[14]      len[2]   chk
  //  '&' FF     0x01: s -> r, end of bulk transmission

  //  '&' FF     0x10: r -> s, clear to send
  //  '&' FF     0x11: r -> s, resend packet
  //                   3
  //                   pkt_nums (0 ~ 16 packets) <- not a good idea since the recieving side need to wait..
  //                   change to one by one resend instead!!!

  //  '&' FF     0x12: r -> s, confirm all received

  //  '&' FF     0x13: s <-> r, request peer to abort (trigger Abort event)

  self.handlePacket = function(packet){
    //packet type: packet[1]
    if(packet[1] === 0xff){
      switch(packet[2]){
        case 0x0: // request to start
          self.fsm.GotPeerSendReq(packet)
          break
        case 0x01: // end of bulk sending
          self.fsm.GotPeerEndPkt()
          break
        case 0x10: // clear to sending
          self.fsm.GotPeerCts()
          break
        case 0x11: // got resend request
          self.fsm.GotResendReq(packet)
          break
        case 0x12: // conrim all received
          self.fsm.GotPeerConfirmation()
          break
        case 0x13: //reset request from Peer
          self.fsm.Abort()
          break
      }
    }else{ //data packet
      self.fsm.GotPeerDataPkt(packet)
    }
  }

  // low level packet writer for all packet
  self.writePacket = function(packet, cb){
    // self.log('write packet: ', packet)
    self.writer(packet)
  }

  // top interface for app to make binary send request
  self.transfer = function(name, data, cb){
    self.fsm.AppTransferReq(name, data)
    self.sendFinishCb = cb
  }

  self.log = function() {
    logger = self.logger;
    if (typeof logger != 'undefined' && logger !== null) {
      var args = Array.prototype.slice.call(arguments);
      logger.apply(null, [args, true]);
    }
    else {
      console.error.apply(null, arguments);
    }
  }
}
